function catalogPosterSlider() {
    var swiper = new Swiper('.catalog-poster .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        loop: true,
        effect: 'fade',
        autoplay: {
            delay: 3000
        },
        navigation: {
            nextEl: '.catalog-poster .swiper-button-next',
            prevEl: '.catalog-poster .swiper-button-prev',
        },
    })
}

function modeSlider() {
    var swiper = new Swiper('.mode .swiper-container', {
        slidesPerView: 6,
        spaceBetween: 0,
        navigation: {
            nextEl: '.mode .swiper-button-next',
            prevEl: '.mode .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 3,
                grid: {
                    rows: 2,
                    fill: 'row',
                },
            },
            993: {
                slidesPerView: 6,
                grid: {
                    rows: 1,
                },
            },
        }
    })
}

catalogPosterSlider()
modeSlider()
